<template>
  <view class="page">


    <view class="debug-visible" />
    <!-- È°∂ÈÉ® -->
    <view class="header">
      <text class="title">ÊàëÁöÑ</text>
    </view>

    <!-- ÂêçÁâá -->
    <view class="card" @click="handleUserCardClick">
      <view class="card-top">
        <view class="avatar-wrap">
          <image class="avatar" :src="userInfo.avatar || '/static/avatar-default.png'" mode="aspectFill" />
        </view>
        <view class="profile">
          <text class="name">{{ userInfo.nickname || 'Áî®Êà∑' }}</text>
          <text class="desc">ÁæéÈ£üÁà±Â•ΩËÄÖ</text>
        </view>
      </view>
      <view class="stats">
        <view class="stat">
          <text class="num">{{ userInfo.followCount || 0 }}</text>
          <text class="label">ÂÖ≥Ê≥®</text>
        </view>
        <view class="divider" />
        <view class="stat">
          <text class="num">{{ userInfo.fansCount || 0 }}</text>
          <text class="label">Á≤â‰∏ù</text>
        </view>
        <view class="divider" />
        <view class="stat">
          <text class="num">{{ userInfo.likeCount || 0 }}</text>
          <text class="label">Ëé∑Ëµû</text>
        </view>
      </view>
    </view>



    <!-- ÊàëÁöÑÔºàÊîæÂú®Â∑•ÂÖ∑‰∏éÊúçÂä°ÂâçÔºâ -->
    <view class="group">
      <view class="group-title">ÊàëÁöÑÂÜÖÂÆπ</view>
      <view class="cell" @click="goFavorites">
        <text class="cell-icon">‚ù§</text><text class="cell-text">ÊàëÁöÑÊî∂Ëóè</text><text class="arrow">‚Ä∫</text>
      </view>
      <view class="cell" @click="goMyComments">
        <text class="cell-icon">üí¨</text><text class="cell-text">ÊàëÁöÑËØÑËÆ∫</text><text class="arrow">‚Ä∫</text>
      </view>
    </view>

    <view class="group">
      <view class="group-title">ÊàëÁöÑ</view>
      <view class="cell" @click="goMyWorks">
        <text class="cell-icon">üñº</text><text class="cell-text">ÊàëÁöÑ‰ΩúÂìÅ</text><text class="arrow">‚Ä∫</text>
      </view>
      <view class="cell" @click="goHistory">
        <text class="cell-icon">üïò</text><text class="cell-text">ÊµèËßàÂéÜÂè≤</text><text class="arrow">‚Ä∫</text>
      </view>
      <view class="cell" @click="goMyInfo">
        <text class="cell-icon">üë§</text><text class="cell-text">ÊàëÁöÑ‰ø°ÊÅØ</text><text class="arrow">‚Ä∫</text>
      </view>
    </view>

    <view class="group">
      <view class="group-title">ÂÖ∂‰ªñ</view>
      <view class="cell" @click="toast('Â∏ÆÂä©‰∏éÂèçÈ¶à')">
        <text class="cell-icon">üéß</text><text class="cell-text">Â∏ÆÂä©‰∏éÂèçÈ¶à</text><text class="arrow">‚Ä∫</text>
      </view>
      <view class="cell" @click="toast('ÂÖ≥‰∫éÊàë‰ª¨')">
        <text class="cell-icon">‚Ñπ</text><text class="cell-text">ÂÖ≥‰∫éÊàë‰ª¨</text><text class="arrow">‚Ä∫</text>
      </view>
    </view>

    <!-- ÈÄÄÂá∫ÁôªÂΩï -->
    <view class="group" v-if="userInfo.isLoggedIn">
      <view class="cell logout-cell" @click="handleLogout">
        <text class="cell-icon">üö™</text>
        <text class="cell-text logout-text">ÈÄÄÂá∫ÁôªÂΩï</text>
        <text class="arrow">‚Ä∫</text>
      </view>
    </view>

    <view style="height: 40rpx;" />
  </view>
</template>

<script>
import { wechatLogin } from '../../api/user.js';
import { request } from '../../api/request.js';
export default {
  data() {
    return {
      userInfo: {
        avatar: '',        // Â§¥ÂÉèÔºàÊú™ÁôªÂΩï‰∏∫Á©∫Ôºâ
        nickname: '',      // ÊòµÁß∞ÔºàÊú™ÁôªÂΩï‰∏∫Á©∫Ôºâ
        followCount: 0,    // ÂÖ≥Ê≥®Êï∞
        fansCount: 0,      // Á≤â‰∏ùÊï∞
        likeCount: 0,      // Ëé∑ËµûÊï∞
        isLoggedIn: false  // ÁôªÂΩïÁä∂ÊÄÅ
      },
      quicks: []
    }
  },
  async onLoad() {
    this.checkLoginStatus();
  },
  methods: {
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
    checkLoginStatus() {
      const token = uni.getStorageSync('token');
      console.log('Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅÔºåtoken:', token); // Ë∞ÉËØïÊó•Âøó
      if (token) {
        this.loadUserInfo();
      } else {
        // Êú™ÁôªÂΩïÁä∂ÊÄÅ‰ΩøÁî®ÈªòËÆ§Êï∞ÊçÆ
        this.userInfo = {
          avatar: '/static/avatar-default.png',
          nickname: 'Áî®Êà∑',
          followCount: 0,
          fansCount: 0,
          likeCount: 0,
          isLoggedIn: false
        };
        console.log('Êú™ÁôªÂΩïÁä∂ÊÄÅÔºåuserInfo:', this.userInfo); // Ë∞ÉËØïÊó•Âøó
      }
    },
    // ÁÇπÂáªÂç°Áâá‰∫ã‰ª∂
    handleUserCardClick() {
      uni.navigateTo({ url: '/pages/profile/edit' });
    },
    // ÊòæÁ§∫ÂæÆ‰ø°ÊéàÊùÉÂºπÁ™ó
    showWechatAuthModal() {
      uni.showModal({
        title: 'ÂæÆ‰ø°ÊéàÊùÉÁôªÂΩï',
        content: 'ËØ∑ÊéàÊùÉÁôªÂΩï‰ª•‰ΩøÁî®ÂÆåÊï¥ÂäüËÉΩ',
        confirmText: 'ÊéàÊùÉÁôªÂΩï',
        cancelText: 'ÂèñÊ∂à',
        success: (res) => {
          if (res.confirm) {
            this.handleWechatLogin();
          }
        }
      });
    },
    // ÂæÆ‰ø°ÊéàÊùÉÁôªÂΩï
    handleWechatLogin() {
      uni.login({
        provider: 'weixin',
        success: (loginRes) => {
          console.log('Ëé∑ÂèñÂà∞ÂæÆ‰ø°code:', loginRes.code);
          // ‰ΩøÁî®codeË∞ÉÁî®ÂêéÁ´ØÁôªÂΩïÊé•Âè£
          this.loginWithCode(loginRes.code);
        },
        fail: (err) => {
          console.error('ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•:', err);
          uni.showToast({ title: 'ÁôªÂΩïÂ§±Ë¥•', icon: 'none' });
        }
      });
    },
    // ‰ΩøÁî®codeË∞ÉÁî®ÂêéÁ´ØÁôªÂΩïÊé•Âè£
    loginWithCode(code) {
      wechatLogin(code)
        .then(res => {
          if (res && res.success) {
            // ‰øùÂ≠òtokenÂà∞Êú¨Âú∞Â≠òÂÇ®
            uni.setStorageSync('token', res.token || res.data?.token);
            uni.showToast({ title: 'ÁôªÂΩïÊàêÂäü' });
            this.checkLoginStatus(); // Âà∑Êñ∞È°µÈù¢Áä∂ÊÄÅ
          } else {
            uni.showToast({ title: res.msg || 'ÁôªÂΩïÂ§±Ë¥•', icon: 'none' });
          }
        })
        .catch(err => {
          console.error('Ë∞ÉÁî®ÁôªÂΩïÊé•Âè£Â§±Ë¥•:', err);
          uni.showToast({ title: 'ÁΩëÁªúÈîôËØØÔºåËØ∑ÈáçËØï', icon: 'none' });
        });
    },
    // Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÔºàÂ∑≤ÁôªÂΩïÁä∂ÊÄÅÔºâ
    async loadUserInfo() {
      try {
        const token = uni.getStorageSync('token');
        console.log('ÂΩìÂâç token:', token, 'Â≠òÂÇ®Êó∂Èó¥:', new Date().toISOString());
        
        if (!token) {
          // Êú™ÁôªÂΩïÁä∂ÊÄÅ‰ΩøÁî®ÈªòËÆ§Êï∞ÊçÆ
          this.userInfo = {
            avatar: '/static/avatar-default.png',
            nickname: 'Áî®Êà∑',
            followCount: 0,
            fansCount: 0,
            likeCount: 0,
            isLoggedIn: false
          };
          return;
        }
        
        // Ê£ÄÊü• token ÊòØÂê¶ÊúâÊïàÔºàÁ§∫‰æãÔºöÈùûÁ©∫‰∏îÈïøÂ∫¶ÂêàÁêÜÔºâ
        if (token.length < 10) {
          uni.showToast({ title: 'ÁôªÂΩïÂá≠ËØÅÊó†ÊïàÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï', icon: 'none' });
          uni.navigateTo({ url: '/pages/login/index' });
          return;
        }
        
        const [res, err] = await new Promise((resolve) => {
          const requestConfig = {
            url: 'http://172.20.10.3:9000/api/user/info',
            method: 'GET',
            header: {
              'token': token.trim(), // ‰øÆÊîπ‰∏∫ÂêéÁ´ØÈ¢ÑÊúüÁöÑÂ§¥ÂêçÁß∞
              'Content-Type': 'application/json'
            },
            timeout: 10000,
            withCredentials: true
          };
          console.log('ËØ∑Ê±ÇÈÖçÁΩÆ:', JSON.stringify(requestConfig, null, 2)); // ÊâìÂç∞ÂÆåÊï¥ÈÖçÁΩÆ
          
          uni.request({
            ...requestConfig,
            complete: (response) => {
              console.log('ËØ∑Ê±ÇÁî®Êà∑‰ø°ÊÅØÂÆåÊàê:', response);
              if (response.statusCode === 401) {
                console.error('Token Êó†ÊïàÔºåÂêéÁ´ØËøîÂõû:', response.data);
              }
              resolve([response, null]);
            },
            fail: (error) => {
              console.error('ËØ∑Ê±ÇÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', error);
              resolve([null, error]);
            }
          });
        });
        
        if (err || !res || res.statusCode !== 200) {
          if (res?.statusCode === 401) {
            uni.removeStorageSync('token');
            uni.showToast({ title: 'ÁôªÂΩïÂ∑≤ËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï', icon: 'none' });
            uni.navigateTo({ url: '/pages/login/index' });
          } else {
            uni.showToast({ title: 'ÁΩëÁªúÈîôËØØÔºåËØ∑ÈáçËØï', icon: 'none' });
          }
          return;
        }
        
        // Êõ¥Êñ∞Áî®Êà∑‰ø°ÊÅØ
        this.userInfo = { ...res.data, isLoggedIn: true };
      } catch (err) {
        console.error('Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', err);
        uni.showToast({ title: 'Ëé∑Âèñ‰ø°ÊÅØÂ§±Ë¥•', icon: 'none' });
      }
    },
    goHistory() {
      uni.navigateTo({ url: '/pages/profile/history' });
    },
    goFavorites() {
      uni.navigateTo({ url: '/pages/profile/favorites' });
    },
    goMyComments() {
      uni.navigateTo({ url: '/pages/profile/comments' });
    },
    goMyWorks() {
      uni.navigateTo({ url: '/pages/profile/works' });
    },
    goMyInfo() {
      uni.navigateTo({ url: '/pages/profile/myinfo' });
    },
    // ÈÄÄÂá∫ÁôªÂΩï
    handleLogout() {
      uni.showModal({
        title: 'Á°ÆËÆ§ÈÄÄÂá∫',
        content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
        success: (res) => {
          if (res.confirm) {
            // Ë∞ÉÁî®ÈÄÄÂá∫ÁôªÂΩïÊé•Âè£
            request('/api/logout', {}, 'POST')
              .then(() => {
                // Ê∏ÖÈô§Êú¨Âú∞Â≠òÂÇ®ÁöÑ token ÂíåÁî®Êà∑‰ø°ÊÅØ
                uni.removeStorageSync('token');
                this.userInfo = {
                  avatar: '/static/avatar-default.png',
                  nickname: 'Áî®Êà∑',
                  followCount: 0,
                  fansCount: 0,
                  likeCount: 0,
                  isLoggedIn: false
                };
                uni.showToast({ title: 'ÈÄÄÂá∫ÁôªÂΩïÊàêÂäü', icon: 'none' });
              })
              .catch(err => {
                console.error('ÈÄÄÂá∫ÁôªÂΩïÂ§±Ë¥•:', err);
                uni.showToast({ title: 'ÈÄÄÂá∫ÁôªÂΩïÂ§±Ë¥•', icon: 'none' });
              });
          }
        }
      });
    }
  }
      }
      
  
</script>

<style>
.page { background: #f7f2e7; min-height: 100vh; position: relative; }
/* AI ÂØπËØùÊ∞îÊ≥°Ôºà‰Ωç‰∫éÂè≥‰∏äËßíÂç°ÈÄöÂõæÂ∑¶‰æßÔºâ */



.debug-visible { height: 2rpx; background: transparent; }

/* È°∂ÈÉ® */
.header {
  height: 88rpx; padding: 0 24rpx;
  display: flex; align-items: center; justify-content: space-between; background: #fff;
}
.title { font-size: 36rpx; font-weight: 700; color: #1a1f36; }
.gear { font-size: 36rpx; color: #6b7280; }

/* ÂêçÁâá */
.card {
  margin: 16rpx 24rpx; border-radius: 24rpx; padding: 20rpx;
  background: #fff; color: #1f2937;
  box-shadow: 0 10rpx 24rpx rgba(0,0,0,0.06);
}
.card-top { display: flex; align-items: center; }
.avatar-wrap { width: 96rpx; height: 96rpx; border-radius: 50%; overflow: hidden; background: rgba(255,255,255,0.25); }
.avatar { width: 100%; height: 100%; }
.profile { margin-left: 16rpx; display: flex; flex-direction: column; }
.name { font-size: 32rpx; font-weight: 700; }
.desc { margin-top: 6rpx; font-size: 24rpx; opacity: 0.9; }
.stats { margin-top: 16rpx; display: flex; align-items: center; justify-content: space-between; }
.stat { flex: 1; text-align: center; }
.num { font-size: 30rpx; font-weight: 700; }
.label { font-size: 22rpx; opacity: 0.9; }
.divider { width: 1rpx; height: 40rpx; background: #e5e7eb; }

/* Âø´Êç∑ÂÖ•Âè£ */
.quick {
  margin: 0 24rpx; padding: 20rpx; background: #fff; border-radius: 24rpx;
  display: flex; flex-wrap: wrap; justify-content: space-between;
  box-shadow: 0 10rpx 24rpx rgba(0,0,0,0.06);
}
.quick-item { width: 25%; padding: 8rpx 0; display: flex; flex-direction: column; align-items: center; box-sizing: border-box; }
.q-icon { font-size: 36rpx; color: #ff6a00; }
.q-text { margin-top: 8rpx; font-size: 24rpx; color: #374151; }

/* ÂàÜÁªÑÂàóË°® */
.group { margin: 16rpx 24rpx; background: #fff; border-radius: 24rpx; box-shadow: 0 10rpx 24rpx rgba(0,0,0,0.06); }
.group-title { padding: 20rpx 20rpx 0; font-size: 26rpx; color: #6b7280; }
.cell { padding: 24rpx 20rpx; display: flex; align-items: center; }
.cell + .cell { border-top: 1rpx solid #f0f2f5; }
.cell-icon { width: 48rpx; text-align: center; font-size: 30rpx; color: #ff6a00; }
.cell-text { flex: 1; font-size: 28rpx; color: #1f2937; }
.arrow { font-size: 36rpx; color: #c7cdd3; }

/* ÈÄÄÂá∫ÁôªÂΩïÊ†∑Âºè */
.logout-cell {
  background: #fff;
}
.logout-text {
  color: #ff4757 !important;
}
/* Âè≥‰∏äËßíÂç°ÈÄöÂõæÊ†∑ÂºèÔºàÁªü‰∏ÄÔºâ */

</style>